body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.cell {
    background-color: rgba(0, 0, 0, 0.8);
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto 100%;
    border: 1px solid rgba(0, 0, 0, 0.8);
    padding: 20px;
    text-align: center;
    font-size: 20px;
    display: flex;
    color: white;
    font-weight: bold;
    overflow: hidden;
    word-break: break-word;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.cell-selected {
    background-image: url(/Tinder-bingo-web/static/media/backgroundt.2abdc64b.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto 70%;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.cell:hover {
    background-color: rgba(0, 0, 0, 0.5);
    cursor: pointer;
}

.cell p {
    margin: auto;
}

@media all and (max-height: 600px) {
    .cell {
        font-size: 12px;
        padding: 9px;
    }
}

@media all and (max-width: 800px) {
    .cell {
        font-size: 2.5vw;
        background-size: auto 70%;
        padding: 9px;
        -webkit-transition: 0.2s;
        transition: 0.2s;
    }
    .cell-selected {
        background-size: auto 40%;
        -webkit-transition: 0.2s;
        transition: 0.2s;
    }
}
.container {
    display: block;
    position: relative;
    padding-left: 30px;
    cursor: pointer;
    font-size: 19px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    color: white;
    margin: 8px 35px 15px 0;

}

.container:hover {
    color: #ccc;
}

.container input {
    position: absolute;
    opacity: 0;
}

.container:hover input ~ .checkmark {
    background: #ccc -webkit-gradient(linear, left top, left bottom, from(#4c4c4c), to(#ccc));
    background: #ccc linear-gradient(to bottom, #4c4c4c, #ccc);
}
.container input:checked ~ .checkmark {
    background: #2196F3 -webkit-gradient(linear, left bottom, left top, from(rgba(5, 132, 242, 1)), to(rgba(10, 175, 241, 1)));
    background: #2196F3 linear-gradient(to top, rgba(5, 132, 242, 1), rgba(10, 175, 241, 1));
}

.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 22px;
    width: 22px;
    background-color: #eee;
    border-radius: 50%;
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

.container input:checked ~ .checkmark:after {
    display: block;
}

.container .checkmark:after {
    top: 8px;
    left: 8px;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: white;
}

@media screen and (max-width: 650px) {
    .container {
        font-size:13px;
        margin: 8px 15px;
        padding-left: 20px;
    }

    .checkmark {
        height: 15px;
        width: 15px;
    }

    .container .checkmark:after {
        top: 5px;
        left: 5px;
        width: 5px;
        height: 5px;
    }
}
body {
  background-color: #282c34;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.App {
  text-align: center;
  display:flex;
  flex-direction: column;
}

.App-header {
  min-height: 15vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: calc(10px + 3vmin);
  color: white;
  width: 100%;
  margin: auto;
  justify-content: space-between;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

a, a:hover, a:active, a:visited {
  color: white;
  text-decoration: none;
}

.logo {
  margin-left: 3vw;
}

.github {
  position: absolute; bottom: 10px; right: 10px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  font-size: 15px;
  max-height: 9vw;
  max-width: 7vw;
}

.github:hover {
  opacity: 0.5;
}

.bingoBoard {
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  background-color: #2196F3;
  max-width: 1000px;
  max-height: 85vh;
  min-height: 85vh;
  position: absolute; top: 0; bottom: 0; left: 0; right: 0;
  overflow: hidden;
  margin: 15vh auto auto;
}

.categories {
  -webkit-transition: 0.1s;
  transition: 0.1s;
  display: flex;
  flex-direction: row;
  margin-top: 5px;
  margin-right: 2vw;
}

.popup {
  z-index: 10000;
  width: 100vw;
  min-height: 100vh;
  font-size: 10vw;
  background:rgba(0,0,0,0.9);
  -webkit-animation: opacity 1s;
          animation: opacity 1s;
  opacity: 0;
  display: none;
  cursor: pointer;
  position:fixed;
}

.show {
  visibility: visible;
  -webkit-animation: opacity 1s;
          animation: opacity 1s;
  opacity: 1;
  display: inherit;
}

.bingotext {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
}

.bingotext p {
  font-size: 2vw;
}

@media all and (max-width: 1150px) {

  .github {
    position: absolute; top: 10px; right: 10px;
  }

  .categories {
    -webkit-transition: 0.2s;
    transition: 0.2s;
    margin-right: 8vw;
  }

}

@media all and (max-width: 650px) {
  .App-header {
    flex-direction: column;
    justify-content: center;
    -webkit-transition: 0.2s;
    transition: 0.2s;
  }

  .logo {
    margin-left: 0;
  }

  .github {
    -webkit-transition: 0.2s;
    transition: 0.2s;
    font-size: 1.7vw;
  }

  .categories {
    margin-right: 0;
  }

}
